<template>
  <div class="text-center">
    <li
      v-for="(item, index) of ordernarArray"
      :key="index"
      @click="aumentar(index)"
      class="list-group-item d-flex justify-content-between align-items-start"
    >
      {{ index }} - {{ item.nombre }}
      <span class="badge bg-primary rounded-pill">{{ item.cantidad }}</span>
    </li>

    <div class="d-grid gap-2">
      <button class="btn btn-danger d-md-block mt-5" @click="reiniciar">
        Reiniciar
      </button>
    </div>
  </div>
</template>

<script>
//mapstate obtiene la variable global
//map mutation modifica las  variables globales
import { mapState, mapMutations } from "vuex";

export default {
  name: "Lista",
  computed: {
    //get vartiables globales
    ...mapState(["frutas"]),
    //ordena el array en base a cantidad
    ordernarArray() {
      return this.frutas.sort((a, b) => b.cantidad - a.cantidad);
    },
  },
  methods: {
    //mutaciones disponibles
    ...mapMutations(["aumentar", "reiniciar"]),
  },
};
</script>
